import baseInfo from "@/components/detail/base-info.vue"
import scrollAttrs from "@/components/detail/scroll-attrs.vue"
import uniListItem from "@/components/uni-ui/uni-list-item/uni-list-item.vue"
import scrollComments from "@/components/detail/scroll-comments.vue"
import uParse from "@/components/uni-ui/uParse/src/wxParse.vue"
import card from "@/components/common/card.vue"
import commonList from "@/components/common/common-list.vue"
import bottomBtn from "@/components/detail/bottom-btn.vue"
import commonPopup from "@/components/common/common-popup.vue"
import price from "@/components/common/price.vue"
import zcmRadioGroup from "@/components/common/radio-group.vue"
import uniNumberBox from "@/components/uni-ui/uni-number-box/uni-number-box.vue"

import {mapState,mapGetters,mapActions,mapMutations} from "vuex"

export default {
	components: {
		baseInfo,
		scrollAttrs,
		uniListItem,
		scrollComments,
		uParse,
		card,
		commonList,
		bottomBtn,
		commonPopup,
		price,
		zcmRadioGroup,
		uniNumberBox,
		// uniNumberBoxx
	},
	computed: {
		...mapState({
			cartlist:state=>state.cart.list,
			// popupShow:state=>state.cart.popupShow,
			changenumBtn:state=>state.cart.changenumBtn,
		}),
		...mapGetters([
			'checkedAll',
			'totalPrice',
			'totalNum',
			'disableSelectAll',
			'popupData'
		]),
		//显示价格
		showPrice(){
			let attrPrice = 0;
			//遍历
			for(let i = 0;i<this.newoptions.length;i++)
			{
				// //先遍历普通规格
				if(this.newoptions[i].isnum == 0)
				{
					//普通规格加钱
					attrPrice += parseFloat(this.newoptions[i].child[this.newoptions[i].selected].addPrice)
				}
			} 
			return attrPrice + this.multiPrice + this.changePrice;
		},
		// 选中的sku
		checkedSkus(){
			//用lamda写法进行遍历
			let checkedSkus = this.newoptions.map(v=>{
			     if(v.isnum != 0 && v.isspecial == 0 && v.ismulti == 0){  //如果是数量选项
					if(v.isnum != 0)
					{
					   return this.coffeePieces + "份" + v.name
					}
				    
				}else if(v.isnum == 0 && v.isspecial == 0 && v.ismulti == 0){ //普通规格
					 return v.child[v.selected]['name'];
				}
			})
			
			checkedSkus = this.$Util.removeEmptyArrayEle(checkedSkus);
			//加上特调规格
			return (checkedSkus.join('/') + ' ' + this.multiAttrStr.join('/')).trim('/')
		},
	},
	data () {
		return{
			//屏幕高度
			height:0,
			accreditshow:false,
			agreeUserInfo:false,
			agreeTemplateMsg:false,
			templatemsgshow:false,
			// 弹框地图start
			id:0, 
			 // 使用 marker点击事件 需要填写id
			title: 'map',
			latitude: 29.909,
			longitude: 106.39742,
			covers: [{
				latitude: 29.909,
				longitude: 106.39742,
				iconPath: '../../static/img/坐标.png'
			}],
			//弹框地图的蒙版+地图板块显示
			mask:false,											
			//有和没有这个特调选项
			specialexist:false,
			//咖啡浓缩数量
			coffeePieces:1,
			//咖啡浓度的数量变化造成的价格变化
			changePrice:0,
			//多选的价格浮动数据 这个是曲线救国的策略
			multiPrice:0,
			//多选选项的规格
			multiAttrStr:[],
			//全屏大购物车
			shopping:false,
			//商品规格
			showattr:false,
			//底部小购物车
			showcart:false,
			//商品规格的特殊选项
			specialcard:false,				
			//当前商品数量
			goodsNum:1,
			detail:{
				num:1
			},
			url:'',
			//动画弹出与否的标识为
			newoptions:[],
			popup:{
				attr:"none",
				express:"none",
				service:"none"
			},
			currentShop:{},
			// 当前点击的商品数据
			currentGoods:{},
			goodsIndex:"",
			showLoading:true,
			//当前选中分类
			activeIndex:0,
			// 左侧种类节点高度
			leftDomsTop:[],
			// 右侧商品节点高度
			rightDomsTop:[], 
			// 左侧种类显示的高度
			leftScrollTop:0,
			// 右侧商品种类显示的高度
			rightScrollTop:0,
			//类别的数据(左侧)
			cate:[],
			//商品的数据(右侧)
			list:[],
			cateItemHeight:0
		}
	},		
	watch:{
		async activeIndex(newValue, oldValue) {
				// 获取scroll-view高度，scrollTop
				let data = await this.getElInfo({
					size:true,
					scrollOffset:true
				})
				//拿到左侧的向上滑动偏移量 以及左侧的高度
				let H = data.height		
				let ST = data.scrollTop
				// console.log(this.leftDomsTop[newValue]+parseFloat(this.cateItemHeight))
				
				// 下边
				if ((this.leftDomsTop[newValue]+parseFloat(this.cateItemHeight)) > (H+ST) ) {
					 return this.leftScrollTop = this.leftDomsTop[newValue]+this.cateItemHeight - H
				}
				// 上边
				if (ST > this.cateItemHeight) {
					this.leftScrollTop = this.leftDomsTop[newValue]
				}
			}
		},

	onReady() {
		
		this.getElInfo({
			all:'left',
			size:true,
			rect:true
		}).then(data=>{
			console.log(data)
			//左节点总向上偏移量的累加计算
			this.leftDomsTop = data.map(v=>{
				  this.cateItemHeight = v.height
				  return v.top
			})
		})
		this.getElInfo({
			all:'right',
			size:true,
			rect:true
		}).then(data=>{
			console.log(data)
			this.rightDomsTop = data.map(v=> v.top)
		})
	},
	
	methods: {
		/**
		 * 显示地图蒙版
		 */
		showMap: function()
		{
			this.mask = true;
		},
		//进行模板消息授权
		showTemp: function() {
			var that = this;
			wx.requestSubscribeMessage({  
			  tmplIds: ['nxyyDOAqE29fWEbvEM96ic-lv7W3LRuTJtO34zihDBk','FwqdpQ5V9rqfOM8vanxGHoxnaMHeyciPDenZssgwj7c'],  
			  success (res) {  //获取成功以后 取消遮罩层和弹窗
				  that.agreeTemplateMsg = false;
				  that.templatemsgshow = false;
				  
				  that.showMap();
			  },  
			  fail (res) {  
				  console.log("fail:"+res.errMsg + "---" + res.errCode);  
			  },  
			  complete (res) {  
				  console.log("complete:"+res.errMsg + "---" + res.errCode);  
			  }  
			})  
		},
		// 获取节点信息
		getElInfo(obj = {}){
			return new Promise((res,rej)=>{
				let option = {
					size:obj.size ? true : false,
					rect:obj.rect ? true : false,
					scrollOffset:obj.scrollOffset ? true : false,
				}
				
				const query = uni.createSelectorQuery().in(this);
				let q = obj.all ? query.selectAll(`.${obj.all}-scroll-item`):query.select('#leftScroll')
				
				console.log(q)
				q.fields(option,data => {
				  res(data)
				}).exec();
			})
		},
		//点击左边当前分类，传值index
		changeCate(index){
			this.activeIndex = index
			// 右边scroll-view滚动到对应区块
			console.log(this.rightDomsTop[index])
			this.rightScrollTop = this.rightDomsTop[index]
		},
		// 监听右边滚动事件
		async onRightScroll(e){
			// 匹配当前scrollTop所处的索引
			this.rightDomsTop.forEach((v,k)=>{
				if (v < e.detail.scrollTop + 3) {
					this.activeIndex = k 
					return false
				}
			})
		},
		iAmSure(){
			this.mask = false
		},
		...mapMutations([
			'addGoodsToCart',
			'login'
		]),
		
		...mapActions([
			'doSelectAll',
			'doDelGoods',
			'doShowPopup',
			'doHidePopup',
			'updateCartList',
			'addCartList'
		]),
		changeCartNum(e,item,index){
			var that = this;
			// 然后往后台发信息 对购物车数量进行相应的改变
			this.addCartList({
				'index':index,
				'num':e,
				'hashKey':item.hashKey,
			}).then(res => {  //请求完以后的操作
				this.updateCartList();  //更新购物车
			})
			//如果购物车清空 所有商品都减为0 那么就进行下面的操作
			if(this.cartlist.length == 0)
			{
				//关闭底部小购物车
				this.showcart = false;
				//打开全屏购物车
				this.shopping = false;
				
				this.specialcard = false;
			}
		},
		//vuex 方法载入
		wxlogin(){
			var that = this;
			console.log(that)
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
				  console.log("loginRes--"+loginRes)
			    //获取code
			    let code = loginRes.code;
				uni.getUserInfo({
					provider: "weixin",
					success:function(infoRes){
						console.log("infoRes--"+infoRes)
						//拼接往后台发送的数据
						var data = {
							code: code,
							user_info: infoRes.rawData,
							encrypted_data:infoRes.encryptedData,
							iv: infoRes.iv,
							signature: infoRes.signature
						}
						 console.log(infoRes.rawData)
						//发起登录请求
						that.$H.post('/mannerdish/user/login',data,{
							// 这里登录是没有token的情况不进行token检测
							token:false,
						}).then((res)=>{
							//vuex登录方法 进行登录状态存储
							that.login(res)
							//跳转操作跳转到登录前的页面
							//关闭弹窗
							uni.showLoading({
								title:"loading",
								mask:true
							})
							that.agreeUserInfo = false;
							that.accreditshow = false;
							
							setTimeout(function(){
								uni.hideLoading()
								//打开消息模板授权
								that.agreeTemplateMsg = true;
								that.templatemsgshow = true;
							},1000)
							
							//登录后拿地址信息
							// this.height = Number(uni.getSystemInfoSync().windowHeight) - 310;
							//这时候that指的是全局，在内部函数想用全局的时候就得用that
							// 获取当前坐标
							// wx.getLocation({
							//     type:'wgs84',
							//     success: function (res) {				
							// 		// 打印一下获取的经纬度
							//         // console.log('当前位置的经度：' + res.longitude);
							//         // console.log('当前位置的纬度：' + res.latitude);
							// 		uni.showLoading({
							// 		    title: '加载中'
							// 		});
							// 		// 请求后台数据
							// 		that.loadData( res.longitude, res.latitude,(res)=>{
							// 		    // 将获取的data数据存入上边的List[]中
							// 		    // that.List = res
							// 		});
							//     }
							// });
							// that.showTemp()
						})
						
					}
				})
				return				 
			  }
			});
		},
		
		/**
		 * 请求数据
		 * @param {Object} cb
		 */
		async loadData(lng,lat,cb)
		{
			//获取数据两种方式
			
			var that = this;
			this.$H.get('/mannerdish/index/index?lng='+lng+'&lat='+lat,{},{
				// 这里测试先不用token				
				token:true,
				cb:function(){
					that.agreeUserInfo = true;
					that.accreditshow = true;
				}
			}).then((res)=>{
				// 最近店铺
				this.data = res
				//店铺列表存为缓存
				this.$Util.setCache("shop_list",res,0);
				this.nearshop.name = this.data[0].name
				this.nearshop.shopaddress = this.data[0].address
				this.nearshop.tel = this.data[0].tel
				//删除下标为0，长度为1的一个数据
				let current_shop = this.data.splice(0,1)
				//其他店铺
				this.list = this.data
				//设置最近店铺为当前店铺 如果用户选择其他店铺 要做店铺改变
				this.$Util.setCache("current_shop",current_shop[0],0);
				// console.log(this.list)
				setTimeout(function () {
				    uni.hideLoading();
				}, 1000);
				//执行回调 如果有的话
				cb && cb(res)
			})				
		},
		clickBtn(){
			//按钮
			this.changenumBtn = !this.changenumBtn
		},
		//咖啡浓缩数量的变化
		coffeePiecesNumber(value,index1,index2)
		{
			//改变浓缩数量
			this.coffeePieces = value
			this.newoptions[index2].isnum = value;
			this.changePrice = 5 * ( Math.ceil(value / 2) - 1);
		},
		
		changeGoodsNumber(value)
		{
			//改变商品数量
			this.goodsNum = value
		},
		//点击弹出规格按钮
		showAttr(){
			this.showattr = !this.showattr
		},
		//点击关闭所有商品规格
		attrOff(){
			//清空当前商品和当前商品数量
			this.coffeePieces = 1;
			this.changePrice = 0;
			this.multiPrice = 0;
			//当前点击的商品
			this.currentGoods = [];
			this.newoptions = [];
			this.goodsNum = 1;
			
			//关闭选项卡
			this.showattr = false;
			this.specialcard = false;
		},
		// 点击弹出购物车按钮,测试用
		showCart(){
			this.showcart = !this.showcart
		},
		//点击购物车，隐藏小购物车，打开全屏购物车
		cart(){
			//关闭底部小购物车
			this.showcart = false
			//打开全屏购物车
			this.shopping = true
		},
		//点击关闭全屏购物车
		off(){
			this.shopping = false
			this.showcart = true
		},
		// 点击规格特调按钮,打开特调规格
		special(){
			this.specialcard = !this.specialcard;
			//特调是否展示判断
		},		
		onShow(option){
			console.log(option)
			var that = this;
			this.currentShop = this.$Util.getCache('current_shop');	
			//地图信息拼接
			this.covers[0].latitude = this.currentShop.lat;
			this.covers[0].longitude = this.currentShop.lng;
			//经纬度的信息复制
			this.latitude = this.currentShop.latitude;
			this.longitude = this.currentShop.longitude;
			//查看option是否存在值
			if(this.$Util.getCache("current_shop_change") == 1){ //这个是有选项的时候 要取后台取餐品信息
				that.cate = [];
				that.list = [];
				//先把餐品信息与分类设置为空 然后去后台取数据  
				that.$H.post('/mannerdish/index/shopinfo?shop_id='+this.currentShop.id,{},{
					token:true,
					cb:function(){ //如果没有登录的回调操作
						that.agreeUserInfo = true;
						that.accreditshow = true;
					}
				}).then((res)=>{
					that.currentShop = res;
					//商品种类
					that.cate = res.cats
					that.list = res.goods_info
					that.url = that.$H.common.ul
				});
				
				this.$Util.setCache("current_shop_change",0);
			}
			//取出已经选择的店铺
			// console.log(this.currentShop)
			//更新购物车
			this.updateCartList(res => {
				if(res.length > 0 )
				{
					that.showcart = true;
				}	
			});
			
			//显示地图
			setTimeout(function(){
				that.showMap();
			},1000)
			
		},
		onLoad(){
			// 获取可用屏幕高度
			this.height = Number(uni.getSystemInfoSync().windowHeight);
			var that = this;			
			//可使用窗口高度减少55,可使用窗口高度是642
			//后面加一个店铺商品判断
			//获取当前坐标
			wx.getLocation({
			    type:'wgs84',
			    success: function (res) {				
					// 打印一下获取的经纬度
			        // console.log('当前位置的经度：' + res.longitude);
			        // console.log('当前位置的纬度：' + res.latitude);
					uni.showLoading({
					    title: '加载中'
					});
					
					//获取坐标以后进行登录判断操作
					that.$H.post('/mannerdish/index/shopinfo?lng='+res.longitude+'&lat='+res.latitude,{},{
						token:true,
						cb:function(){ //如果没有登录的回调操作
							that.agreeUserInfo = true;
							that.accreditshow = true;
						}
					}).then((res)=>{
						that.currentShop = res;
						that.$Util.setCache("current_shop",res,0);
						//商品种类6
						that.cate = res.cats
						that.list = res.goods_info
						that.url = that.$H.common.ul
					});
			      },
		      })
		},
		
		changeNum(e,item){
		    this.currentGoods.num = e
		},
		// 加入购物车
		addCart(){
			// 组织数据,点击当前商品，获取当前商品
			let goods = this.currentGoods;
			//当前选中的
			goods['option']['options'] = this.newoptions
			//当前规格str
			goods['checkedSku'] = this.checkedSkus
			//当前商品总价
			goods['showPrice'] = this.showPrice
			//选中状态
			goods['checked'] = true
			//当前商品数量
			goods['num'] = this.goodsNum
			// 加入购物车 本地添加
			//this.addGoodsToCart(goods)
			//加入购物车 直接线上加入redis缓存
			this.$H.post('/mannerdish/cart/updateCart',{
				goods_info:JSON.stringify(this.currentGoods)
			},{
				token:true
			}).then(res=>{
				// 隐藏筛选框
				this.hide('attr')
				// 成功提示
				uni.showToast({
					title: '加入成功'
				});
				//清空当前商品和当前商品数量
				this.coffeePieces = 1;
				this.changePrice = 0;
				this.multiPrice = 0;
				this.currentGoods = [];
				this.newoptions = [];
				this.goodsNum = 1;
				
				//关闭选项卡
				this.showattr = false;
				this.specialcard = false;
				//更新购物车
				this.updateCartList();
				this.off();
			})
		},
		
		/**
		 * 隐藏商品规格
		 * @param {Object} key
		 */
		hide(key){
			this.popup[key] = 'hide'
			setTimeout(()=>{
				this.popup[key] = 'none'
			}, 200);
		},
		
		/**
		 * 商品点击后显示规格弹窗
		 * @param {Object} key
		 * @param {Object} index2
		 */
		show(index,index2){
			//确认当前商品下标
			this.goodsIndex = index2
			//当前点击的商品信息，把点击的信息传递到currentGoods{}内
			var currentGoods = this.$Util.deepClone(this.list[index]['goods'][this.goodsIndex]);
			this.currentGoods = currentGoods
		
			this.currentGoods.num = 1;
			//设置当前的规格
		    var newoptions =  this.$Util.deepClone(this.list[index]['goods'][this.goodsIndex]['option']['options']);
			
			console.log(this.currentGoods)
			this.specialexist = false
			//看是否有特调
			for(let i = 0;i<newoptions.length;i++){
				console.log('进来了')
				if(newoptions[i].isspecial == 1){
					this.specialexist = true
				}
			}
			
			this.newoptions = newoptions;
			console.log(this.newoptions)
			//显示规格
			this.showattr = true;
			//关闭底部购物车的按钮
			this.showcart = false;
		},
		preview(src, e) {
			// do something
			console.log("src: " + src);
		},
		navigate(href, e) {
			// 如允许点击超链接跳转，则应该打开一个新页面，并传入href，由新页面内嵌webview组件负责显示该链接内容
			console.log("href: " + href);
		},
		
		/**
		 * 选择店铺
		 */
		selectShop()
		{
			//跳转导选择店铺的页面
			uni.navigateTo({
				url:"../../pages/mannershop/listshop"
			})
		},
		
		//打开地图
		openMap()
		{
			let obj = {
				latitude: parseFloat(this.currentShop.lat),
				longitude: parseFloat(this.currentShop.lng),
				name:  this.currentShop.address + this.currentShop.name
			}
			uni.openLocation(obj)
		},
		
		//单选项选择事件
		selectSimple(index,index2){
			this.newoptions[index].selected = index2;
		},
		//多选选择时间
		selectMulti(index,index2){
			let addPrice = 0;
			let attrStr = [];
			//取反操作
			this.$data.newoptions[index].child[index2].selected = !this.$data.newoptions[index].child[index2].selected ? 1 : 0 
			this.$forceUpdate()
			
			for(let i = 0;i<this.newoptions.length;i++)
			{
				//遍历加价钱的多选项
				if( this.newoptions[i].isnum == 0 && this.newoptions[i].isspecial == 1 && this.newoptions[i].ismulti == 1 ){ //多选加规格
					for(let j = 0;j<this.newoptions[i].child.length;j++)
					{
						if(this.newoptions[i].child[j].selected)
						{
							//多选规格加价钱
							addPrice += parseFloat(this.newoptions[i].child[j].addPrice)
							//规格内容
							attrStr.push(this.newoptions[i].child[j].name)
						}
					}
				}
			}
			//当前多选选项的规格
			this.multiAttrStr = attrStr
			//总价增加
			this.multiPrice = addPrice
		},
		/**
		 * 提交订单
		 */
		submitOrder()
		{
			//1 购物车订单数据入缓存
			this.$Util.setCache("current_order_info",this.cartlist,0);
			//2 跳转到订单结算页面 ordersubmit
			uni.navigateTo({
				url:"../../pages/mannerorder/ordersubmit"
			})
		},
	}	
}
